import { Component, OnInit } from '@angular/core';
import { Vendor } from '../view-vendor/vendor';
import { Router, ActivatedRoute } from '@angular/router';
import { SaveVendorService } from '../save-vendor.service';
import { NgForm } from '@angular/forms';

@Component({
  selector: 'app-save-vendor',
  templateUrl: './save-vendor.component.html',
  styleUrls: ['./save-vendor.component.scss']
})
export class SaveVendorComponent implements OnInit {

  vendors: Vendor[];
  vendor = new Vendor();
  vendorId: any;

  constructor(private router: Router, private service: SaveVendorService, private route: ActivatedRoute) { }

  ngOnInit() {
    this.route.params.subscribe(params => this.getVendorById(params['id']));
    this.resetForm();
  }
  addVendor(): void {
    this.service.insertVendor(this.vendor)
      .subscribe((response) => {
        console.log(response);

        this.getVendor;
        this.router.navigate(['viewVendor'])
        this.resetForm();
      }, (error) => {
        console.log(error);
      });
  }
  resetForm(form?: NgForm) {
    if (form != null)
      form.resetForm();
    this.service.formData = {

      vendorId: '',
      vendorName: '',
      address: '',
      location: '',
      service: '',
      pincode: '',
      isActive: '',
      contactId: '',
      name: '',
      dept: '',
      email: '',
      phone: ''
    }
  }
  getVendor(): void {
    this.service.getAllVendors()
      .subscribe((data) => {
        this.vendors = data;
        console.log(data);

      }, (error) => {
        console.log(error);

      });
  }

  getVendorById(vendorId: any) {
    this.vendorId = vendorId;
    console.log(this.vendorId);
    this.service.getVendorById(this.vendorId)
      .subscribe((vendorbyId) => {
        this.vendor = vendorbyId;
        console.log(vendorbyId);
      }, (error) => {
        console.log(error);
      });
  }

  backToVendor() {
    this.router.navigate(['viewVendor'])
  }
}
