import { Component, OnInit } from '@angular/core';
import { SaveVendorService } from '../save-vendor.service';
import { ActivatedRoute } from '@angular/router';
import { Vendor } from '../view-vendor/vendor';

@Component({
  selector: 'app-edit-vendor',
  templateUrl: './edit-vendor.component.html',
  styleUrls: ['./edit-vendor.component.scss']
})
export class EditVendorComponent implements OnInit {
  vendorId: any;
  vendor = new Vendor();
  constructor(private service: SaveVendorService, private route: ActivatedRoute) { }

  ngOnInit() {
  }
  getVendorById() {
    this.vendorId = this.route.snapshot.paramMap.get('id');
    console.log(this.vendorId);
    this.service.getVendorById(this.vendorId)
      .subscribe((vendorbyId) => {
        this.vendor = vendorbyId;
        console.log(vendorbyId);
      }, (error) => {
        console.log(error);
      });
  }



}
